import React from 'react'
import { useState } from 'react';
import './Price.scss'

import Range from '../Range/Range';

function Price(props) {
    const [isActive, setIsActive] = useState(false);
    const handleClick = event => {
        // 👇️ toggle isActive state on click
        setIsActive(current => !current);
    };

    const [choice, setChoice] = useState("apple");

    const fruits = ["Валюта, USD", "Валюта, EUR"];
    const options = fruits.map((fruit) => {
        return <option value={fruit}>{fruit}</option>;
    });
    console.log(choice);
    const handleFruit = (event) => {
        setChoice(event.target.value);
    };

    return (
        <div className="categories scroll">
            <div className={isActive ? 'opened3' : 'closed'}>
                <div onClick={handleClick} className="topwrap">
                    <div className="title">{props.title}</div>
                    <button className={isActive ? 'opened-button' : 'closed-button'} ></button>
                </div>
                <div className="selector">
                    {/* <label htmlFor="select0" class="select">

                        <input type="radio" name="list" value="not_changed" id="select0" />
                        <div className="items">
                            <input type="radio" name="list" value="first_value" id="list0[0]" />
                            <label htmlFor="list0[0]">Валюта, USD</label>
                            <input type="radio" name="list" value="second_value" id="list0[1]" />
                            <label htmlFor="list0[1]">Валюта, EUR</label>
                            <span id="text">Выберите валюту</span>
                        </div>
                    </label> */}
                    <select onChange={handleFruit}>{options}</select>
                </div>
                <div className="ranger">
                    <div className="ranger__item from"><Range className="range" /></div>

                </div>
            </div>

        </div>
    )
}

export default Price